import { Component, OnInit, ChangeDetectorRef, Inject } from "@angular/core";
import { DomSanitizer } from "@angular/platform-browser";
import { AbstractPlugin } from "../abstract-plugin";
import { PluginWrapperComponent, PluginConfig, PLUGIN_CONTEXT } from "shared";
import { takeUntil } from "rxjs/operators";

@Component({
  selector: "app-text",
  templateUrl: "./text.component.html",
  styleUrls: ["./text.component.scss"],
})
export class TextComponent implements AbstractPlugin, OnInit {
  constructor(
    private sanitizer: DomSanitizer,
    private cd: ChangeDetectorRef,
    @Inject(PLUGIN_CONTEXT) public context: PluginWrapperComponent
  ) {}

  sanitizedVal;
  ngOnInit() {
    this.sanitizedVal = this.sanitizer.bypassSecurityTrustHtml(this.context.pluginConfig.value);
    this.context.update$.pipe(takeUntil(this.context.destroy$)).subscribe((update: any) => {
      this.sanitizedVal = this.sanitizer.bypassSecurityTrustHtml(this.context.pluginConfig.value);
    });
    // setInterval(() => {
    //   this.context.pluginConfig.value += " lorem";
    //   this.sanitizedVal = this.sanitizer.bypassSecurityTrustHtml(this.context.pluginConfig.value);

    //
    //   // this.sanitizedVal = this.sanitizedVal + this.sanitizedVal;
    //   this.cd.detectChanges();
    //   // this.context.contentChanged$.next();
    // }, 2000);
  }
}
